.container {
  margin: 50px 0;
}

.container .card {
  margin: 0 2%;
}

.infected {
  border-bottom: 10px solid rgba(0, 0, 255);
  border-top: 1px solid rgba(0, 0, 255, 0.5);
  border-left: 1px solid rgba(0, 0, 255, 0.5);
  border-right: 1px solid rgba(0, 0, 255, 0.5);
}

.recovered {
  border-bottom: 10px solid rgba(0, 255, 0);
  border-top: 1px solid rgba(0, 255, 0, 0.5);
  border-right: 1px solid rgba(0, 255, 0, 0.5);
  border-left: 1px solid rgba(0, 255, 0, 0.5);
}

.deaths {
  border-bottom: 10px solid rgba(255, 0, 0);
  border-top: 1px solid rgba(255, 0, 0, 0.5);
  border-left: 1px solid rgba(255, 0, 0, 0.5);
  border-right: 1px solid rgba(255, 0, 0, 0.5);
}

.cardLabel {
  text-transform: capitalize;
}